import kotlinx.css.px
import kotlinx.css.width
import kotlinx.html.js.onClickFunction
import kotlinx.html.role
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledButton
import styled.styledDiv

external interface TileQuestionCardProps: RProps {
    var activeTile: TileItem
    var tileTimerState: TileTimerState
    var thirukkuralState: ThirukkuralState
    var onTileShowAnswerClick: () -> Unit
    var onBackClick: (Boolean) -> Unit
}

class TileQuestionCard : RComponent<TileQuestionCardProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = mutableListOf("card m-2")
            }
            firstRound {
                selectedTopic = props.activeTile.type
                selectedThirukkural = props.activeTile.thirukkural
                selectedSol = props.activeTile.sol
                showAnswer = props.activeTile.showAnswer
                thirukkuralState = props.thirukkuralState
                tileTimerState = props.tileTimerState
            }
            styledDiv {
                css {
                    classes = mutableListOf("card-footer d-flex justify-content-between p-2")
                }
                styledButton {
                    css {
                        classes = mutableListOf("btn btn-danger")
                    }
                    +""
                    attrs {
                        onClickFunction = {
                            props.onTileShowAnswerClick()
                        }
                    }
                }
                styledDiv {
                    css {
                        classes = mutableListOf("btn-group")
                    }
                    attrs {
                        role = "group"
                    }
                    styledButton {
                        css {
                            classes = mutableListOf("btn btn-success")
                            width = 100.px
                        }
                        +""
                        attrs {
                            onClickFunction = {
                                props.onBackClick(true)
                            }
                        }
                    }
                    styledButton {
                        css {
                            classes = mutableListOf("btn btn-danger")
                            width = 100.px
                        }
                        +""
                        attrs {
                            onClickFunction = {
                                props.onBackClick(false)
                            }
                        }
                    }
                }
            }
        }
    }
}

fun RBuilder.tileQuestionCard(handler: TileQuestionCardProps.() -> Unit): ReactElement {
    return child(TileQuestionCard::class) {
        this.attrs(handler)
    }
}
