import kotlinx.browser.window
import kotlinx.coroutines.MainScope
import kotlinx.coroutines.await
import kotlinx.coroutines.launch
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.setState
import styled.css
import styled.styledDiv

suspend fun fetchSource(): List<Thirukkural> {
    val sourceUrl = "https://raw.githubusercontent.com/dreamuth/dreamuth.github.io/master/kurals.txt"
    val sourceData = window.fetch(sourceUrl).await().text().await()
    val thirukkurals = readSource(sourceData)
    println("version: 0.9")
    println("Source: $sourceUrl loaded")
    return thirukkurals
}

external interface AppState: RState {
    var loaded: Boolean
    var selectedTopic: Topic
    var selectedPerson: String
    var thirukkurals: List<Thirukkural>
    var athikaramState: AthikaramState
    var thirukkuralState: ThirukkuralState
    var wordState: WordState
    var filteredKuralState: FilteredKuralState
    var letterState: LetterState
    var statisticsState: StatisticsState
    var tileState: TileState
    var persons: List<String>
    var showAnswer: Boolean
    var timer: Timer
    var tileTimerState: TileTimerState
}

class App : RComponent<RProps, AppState>() {
    override fun AppState.init() {
        val mainScope = MainScope()
        mainScope.launch {
            val result = fetchSource()
            setState {
                thirukkurals = result
                selectedTopic = Topic.Athikaram
                athikaramState = AthikaramState(thirukkurals)
                thirukkuralState = ThirukkuralState(thirukkurals)
                filteredKuralState = FilteredKuralState(thirukkurals)
                statisticsState = StatisticsState(thirukkurals)
                wordState = WordState(thirukkurals)
                letterState = LetterState(thirukkuralState.getCurrent())
                persons = listOf("") + thirukkurals.map { it.person }.distinct()
                timer = Timer(show = true)
                tileTimerState = TileTimerState()
                tileState = TileState(thirukkurals)
                selectedPerson = persons[0]
                loaded = true
                println("No of athikarams: ${athikaramState.athikarams.size}")
                println("No of kurals: ${thirukkurals.size}")
                println("No of unique words from all kurals: ${wordState.words.size}")
                window.setInterval(timerHandler(), 1000)
            }
        }
    }

    private fun timerHandler(): () -> Unit = {
        if (state.timer.show) {
            setState {
                timer = timer.copy(time = timer.time + 1)
            }
        }
        if (state.selectedTopic == Topic.Tile) {
            if (state.tileTimerState.time > 0) {
                setState {
                    tileTimerState.time--
                }
            }
        }
    }

    override fun RBuilder.render() {
        hotKeys {
            attrs.keyMap = MyKey(arrayOf("s", "n", "t"))
            attrs.handlers = MyHandler {
                when (it.asDynamic()["key"]) {
                    "s" -> onShowAnswerHandler(true)
                    "n" -> onNextClickHandler(state.selectedTopic)
                    "t" -> println("t pressed")
                }
            }
            styledDiv {
                css {
                    classes = mutableListOf("alert alert-primary text-center mb-0")
                }
                +" "
            }
            styledDiv {
                css {
                    classes = mutableListOf("container-lg pl-0 pr-0")
                }
                if (state.loaded) {
                    person {
                        thirukkurals = state.thirukkurals
                        athikaram = state.athikaramState
                        thirukkuralState = state.thirukkuralState
                        wordState = state.wordState
                        timer = state.timer
                        persons = state.persons
                        showAnswer = state.showAnswer
                        selectedTopic = state.selectedTopic
                        selectedPerson = state.selectedPerson
                        filteredKuralState = state.filteredKuralState
                        letterState = state.letterState
                        statisticsState = state.statisticsState
                        tileState = state.tileState
                        tileTimerState = state.tileTimerState
                        onPersonClick = { personName ->
                            setState {
                                if (state.selectedPerson != personName) {
                                    showAnswer = false
                                    selectedPerson = personName
                                    thirukkuralState = if (selectedPerson == "") {
                                        ThirukkuralState(thirukkurals)
                                    } else {
                                        ThirukkuralState(thirukkurals.filter { it.person == personName })
                                    }
                                    statisticsState = StatisticsState(thirukkuralState.kurals)
                                    athikaramState = AthikaramState(thirukkuralState.kurals)
                                    wordState = WordState(thirukkuralState.kurals)
                                    filteredKuralState = FilteredKuralState(thirukkuralState.kurals)
                                    letterState = LetterState(thirukkuralState.getCurrent())
                                    timer = timer.copy(time = 0, count = 0)
                                    tileTimerState = TileTimerState()
                                    tileState = TileState(thirukkuralState.kurals)
                                }
                            }
                        }
                        onTopicClick = { topic ->
                            setState {
                                if (selectedTopic != topic) {
                                    showAnswer = false
                                    selectedTopic = topic
                                    timer = timer.copy(time = 0, count = 0)
                                }
                            }
                        }
                        onShowAnswerClick = { show ->
                            onShowAnswerHandler(show)
                        }
                        onTimerClick = {
                            setState {
                                timer = timer.copy(time = 0, show = !timer.show, count = 0)
                            }
                        }
                        onNextClick = {
                            onNextClickHandler(selectedTopic)
                        }
                        onLetterClick = { letter ->
                            for (i in 0..1) {
                                checkLetter(
                                    letter,
                                    state.letterState.kuralWordsSrc[i],
                                    state.letterState.kuralWordsTarget[i]
                                )
                            }
                            setState {
                                letterState.elethukkal.forEach { row ->
                                    row.entries.forEach { entry ->
                                        if (entry.key == letter) {
                                            entry.setValue(false)
                                        }
                                    }
                                }
                                letterState = letterState.copy(noOfAttempts = letterState.noOfAttempts + 1)
                            }
                        }
                        onFilterClick = { index ->
                            setState {
                                filteredKuralState = if (index == 0) {
                                    thirukkuralState = if (selectedPerson == "") {
                                        ThirukkuralState(thirukkurals)
                                    } else {
                                        ThirukkuralState(thirukkurals.filter { it.person == selectedPerson })
                                    }
                                    statisticsState = StatisticsState(thirukkuralState.kurals)
                                    athikaramState = AthikaramState(thirukkuralState.kurals)
                                    wordState = WordState(thirukkuralState.kurals)
                                    letterState = LetterState(thirukkuralState.getCurrent())
                                    FilteredKuralState(thirukkuralState.kurals)
                                } else {
                                    thirukkuralState = if (selectedPerson == "") {
                                        ThirukkuralState(thirukkurals.filter {
                                            getCharList(it.words[0]).size == filteredKuralState.wordSizes[index] })
                                    } else {
                                        ThirukkuralState(thirukkurals.filter { it.person == selectedPerson }
                                            .filter { getCharList(it.words[0]).size == filteredKuralState.wordSizes[index] })
                                    }
                                    statisticsState = StatisticsState(thirukkuralState.kurals)
                                    athikaramState = AthikaramState(thirukkuralState.kurals)
                                    wordState = WordState(thirukkuralState.kurals)
                                    letterState = LetterState(thirukkuralState.getCurrent())
                                    filteredKuralState.copy(index = index, kurals = thirukkuralState.kurals)
                                }
                            }
                        }
                        onTileItemClick = { activeTile ->
                            setState {
                                tileState.activeTileIndex = activeTile
                            }
                        }
                        onBackClick = { answered ->
                            setState {
                                tileState.activeTileIndex?.let {
                                    if (answered && !tileState.tiles[it].answered && tileTimerState.time > 0) {
                                        tileTimerState.count++
                                        tileState.tiles[it].answered = true
                                    }
                                }
                                tileState.activeTileIndex = null
                            }
                        }
                        onTileShowAnswerClick = {
                            setState {
                                tileState.activeTileIndex?.let {
                                    tileState.tiles[it].showAnswer = true
                                }
                            }
                        }
                        onRetryClick = {
                            setState {
                                tileTimerState = TileTimerState()
                                tileState = TileState(thirukkuralState.kurals)
                            }
                        }
                    }
                }
            }
        }
    }

    private fun onShowAnswerHandler(show: Boolean) {
        setState {
            showAnswer = show
            if (selectedTopic == Topic.KuralEluthu) {
                letterState = letterState.copy(
                    kuralWordsTarget = fillLetters(letterState.thirukkural),
                    elethukkal = disableButtons()
                )
            }
        }
    }

    private fun onNextClickHandler(topic: Topic) {
        onShowAnswerHandler(false)
        setState {
            timer = timer.copy(count = timer.count + 1)
        }
        when (topic) {
            Topic.Athikaram, Topic.AthikaramPorul -> {
                setState {
                    athikaramState = athikaramState.getNext()
                }
            }
            Topic.KuralPorul, Topic.Image -> {
                setState {
                    thirukkuralState = thirukkuralState.getNext()
                }
            }
            Topic.KuralSol -> {
                setState {
                    wordState = wordState.getNext()
                }
            }
            Topic.KuralEluthu -> {
                setState {
                    thirukkuralState = thirukkuralState.getNext()
                    letterState = LetterState(thirukkuralState.getCurrent())
                }
            }
            Topic.Statistics, Topic.AllKurals, Topic.Tile -> {
                // Next button not available
            }
        }
    }

    private fun checkLetter(letter: String, sourceList: List<List<String>>, targetLine: List<MutableList<String>>) {
        for (i in sourceList.indices) {
            val wordList = sourceList[i]
            for (j in wordList.indices) {
                val keyLetter = getKeyLetter(wordList[j])
                if (letter == keyLetter) {
                    setState {
                        targetLine[i][j] = wordList[j]
                    }
                }
            }
        }
    }

    private fun getKeyLetter(char: String) =
        keyLetters[char] ?: error("Missing $char ${char.toCharArray()[0].toInt()}")
}

fun getWords(line: String): List<String> {
    val list = mutableListOf<String>()
    var currentWord = ""
    val charArray = line.toCharArray()
    for (char in charArray) {
        when (char.toInt()) {
            // Space, Dot, Zero-width non-joiner
            32, 39, 44, 46, 58, 8204 -> {
                if (currentWord.isNotBlank()) {
                    list.add(currentWord)
                }
                currentWord = ""
            }
            else -> {
                currentWord += char
            }
        }
    }
    if (currentWord.isNotBlank()) list.add(currentWord)
    return list
}
