import kotlinx.css.LinearDimension
import kotlinx.css.width
import kotlinx.html.js.onClickFunction
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledButton
import styled.styledDiv
import styled.styledSpan

external interface TitleBarProps: RProps {
    var persons: List<String>
    var selectedPerson: String
    var selectedTopic: Topic
    var filteredKuralState: FilteredKuralState
    var tileTimerState: TileTimerState
    var timer: Timer
    var firstRowStyle: String
    var firstRowWidth: LinearDimension?
    var personButtonWidth: LinearDimension?
    var topicButtonWidth: LinearDimension?
    var secondOptionalRowStyle: String?
    var secondRowStyle: String
    var secondRowWidth: LinearDimension?
    var allKuralsWidth: LinearDimension?
    var navigationWidth: LinearDimension
    var onPersonClick: (String) -> Unit
    var onTopicClick: (Topic) -> Unit
    var onFilterClick: (Int) -> Unit
    var onShowAnswerClick: (Boolean) -> Unit
    var onNextClick: () -> Unit
    var onTimerClick: () -> Unit
    var onRetryClick: () -> Unit
}

class TitleBar : RComponent<TitleBarProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = mutableListOf("row m-2")
            }
            styledDiv {
                css {
                    classes = mutableListOf(props.firstRowStyle)
                    props.firstRowWidth?.let { width = it }
                }
                styledDiv {
                    css {
                        classes = mutableListOf("btn-group mr-2")
                        props.personButtonWidth?.let { width = it }
                    }
                    dropdown {
                        id = "personDropDown"
                        names = listOf(props.persons)
                        selectedName = props.selectedPerson
                        onDropdownClick = { _, name -> props.onPersonClick(name) }
                    }
                }
                styledDiv {
                    css {
                        classes = mutableListOf("btn-group")
                        props.topicButtonWidth?.let { width = it }
                    }
                    dropdown {
                        id = "topicDropDown"
                        names = listOf(
                            listOf(
                                Topic.Athikaram.tamil,
                                Topic.KuralSol.tamil,
                                Topic.Image.tamil,
                                Topic.KuralPorul.tamil,
                                Topic.AthikaramPorul.tamil
                            ),
                            listOf(Topic.Tile.tamil),
                            listOf(Topic.KuralEluthu.tamil),
                            listOf(Topic.Statistics.tamil, Topic.AllKurals.tamil)
                        )
                        selectedName = props.selectedTopic.tamil
                        onDropdownClick = { _, name ->
                            props.onTopicClick(Topic.getTopic(name))
                        }
                    }
                }
            }
            styledDiv {
                css {
                    props.secondOptionalRowStyle?.let { classes = mutableListOf(it) }
                    props.secondRowWidth?.let { width = it }
                }
                styledDiv {
                    css {
                        classes = mutableListOf(props.secondRowStyle)
                        props.secondRowWidth?.let { width = it }
                    }
                    when (props.selectedTopic) {
                        Topic.AllKurals -> {
                            dropdown {
                                id = "allKuralsDropdown"
                                names = listOf(props.filteredKuralState.wordSizes.map {
                                    if (it == 0) "  " else "$it     "
                                })
                                selectedName = names[0][props.filteredKuralState.index]
                                badge = props.filteredKuralState.kurals.size
                                onDropdownClick = { index, _ -> props.onFilterClick(index)}
                                props.allKuralsWidth?.let { width = it }
                            }
                        }
                        Topic.Tile -> {
                            tileTimer {
                                buttonSize = props.navigationWidth
                                tileTimerState = props.tileTimerState
                                onRetryClick = {
                                    props.onRetryClick()
                                }
                            }
                        }
                        else -> {
                            navigation {
                                buttonSize = props.navigationWidth
                                timer = props.timer
                                onShowAnswerClick = {
                                    props.onShowAnswerClick(true)
                                }
                                onNextClick = {
                                    props.onShowAnswerClick(false)
                                    props.onNextClick()
                                }
                                onTimerClick = {
                                    props.onTimerClick()
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

fun RBuilder.titleBar(handler: TitleBarProps.() -> Unit): ReactElement {
    return child(TitleBar::class) {
        this.attrs(handler)
    }
}
