import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement

external interface TopicPros: RProps {
    var selectedTopic: Topic
    var showAnswer: Boolean
    var allThirukkurals: List<Thirukkural>
    var athikaram: AthikaramState
    var thirukkuralState: ThirukkuralState
    var wordState: WordState
    var letterState: LetterState
    var filteredKuralState: FilteredKuralState
    var statisticsState: StatisticsState
    var tileState: TileState
    var tileTimerState: TileTimerState
    var timer: Timer
    var onShowAnswerClick: (Boolean) -> Unit
    var onTileShowAnswerClick: () -> Unit
    var onTimerClick: () -> Unit
    var onNextClick: () -> Unit
    var onLetterClick: (String) -> Unit
    var onFilterClick: (Int) -> Unit
    var onTileItemClick: (Int) -> Unit
    var onBackClick: (Boolean) -> Unit
}

class TopicContent: RComponent<TopicPros, RState>() {
    override fun RBuilder.render() {
        when (props.selectedTopic) {
            Topic.Athikaram -> {
                athikaram {
                    selectedThirukkurals = getThirukuralsForAthikaram()
                    showAnswer = props.showAnswer
                }
            }
            Topic.AthikaramPorul -> {
                athikaramPorul {
                    selectedThirukkurals = getThirukuralsForAthikaram()
                    showAnswer = props.showAnswer
                }
            }
            Topic.KuralPorul -> {
                kuralPorul {
                    selectedThirukkural = props.thirukkuralState.getCurrent()
                    showAnswer = props.showAnswer
                }
            }
            Topic.Image -> {
                image {
                    selectedThirukkural = props.thirukkuralState.getCurrent()
                    showAnswer = props.showAnswer
                }
            }
            Topic.KuralSol -> {
                sol {
                    question = props.wordState.getCurrent()
                    selectedThirukkurals = props.thirukkuralState.kurals.filter { it.words.contains(question) }
                    showAnswer = props.showAnswer
                }
            }
            Topic.Tile -> {
                if (props.tileState.activeTileIndex == null) {
                    tile {
                        tileState = props.tileState
                        tileTimerState = props.tileTimerState
                        onTileItemClick = props.onTileItemClick
                    }
                } else {
                    tileQuestionCard {
                        activeTile = props.tileState.tiles[props.tileState.activeTileIndex!!]
                        tileTimerState = props.tileTimerState
                        thirukkuralState = props.thirukkuralState
                        onTileShowAnswerClick = props.onTileShowAnswerClick
                        onBackClick = props.onBackClick
                    }
                }
            }
            Topic.KuralEluthu -> {
                kuralEluthu {
                    letterState = props.letterState
                    showAnswer = props.showAnswer
                    onLetterClick = { letter -> props.onLetterClick(letter) }
                }
            }
            Topic.Statistics -> {
                statisticsDisplay {
                    statisticsState = props.statisticsState
                }
            }
            Topic.AllKurals -> {
                allKurals {
                    filteredKuralState = props.filteredKuralState
                    onFilterClick = { props.onFilterClick(it) }
                }
            }
        }
    }

    private fun getThirukuralsForAthikaram() = props.thirukkuralState.kurals
        .filter { it.athikaram == props.athikaram.getCurrent() }
}

fun RBuilder.topicContent(handler: TopicPros.() -> Unit): ReactElement {
    return child(TopicContent::class) {
        this.attrs(handler)
    }
}
