import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement

external interface FirstRoundProps: RProps {
    var selectedTopic: Topic
    var selectedThirukkural: Thirukkural
    var selectedSol: String
    var showAnswer: Boolean
    var thirukkuralState: ThirukkuralState
    var tileTimerState: TileTimerState
}

class FirstRound : RComponent<FirstRoundProps, RState>() {
    override fun RBuilder.render() {
        when (props.selectedTopic) {
            Topic.Athikaram -> {
                athikaram {
                    selectedThirukkurals = getThirukuralsForAthikaram()
                    showAnswer = props.showAnswer
                }
            }
            Topic.AthikaramPorul -> {
                athikaramPorul {
                    selectedThirukkurals = getThirukuralsForAthikaram()
                    showAnswer = props.showAnswer
                }
            }
            Topic.KuralPorul -> {
                kuralPorul {
                    selectedThirukkural = props.selectedThirukkural
                    showAnswer = props.showAnswer
                }
            }
            Topic.Image -> {
                image {
                    selectedThirukkural = props.selectedThirukkural
                    showAnswer = props.showAnswer
                }
            }
            Topic.KuralSol -> {
                sol {
                    question = props.selectedSol
                    selectedThirukkurals = props.thirukkuralState.kurals.filter { it.words.contains(question) }
                    showAnswer = props.showAnswer
                }
            }
            else -> {}
        }
    }

    private fun getThirukuralsForAthikaram() = props.thirukkuralState.kurals
        .filter { it.athikaram == props.selectedThirukkural.athikaram }
}

fun RBuilder.firstRound(handler: FirstRoundProps.() -> Unit): ReactElement {
    return child(FirstRound::class) {
        this.attrs(handler)
    }
}
