import kotlinx.css.LinearDimension
import kotlinx.css.width
import kotlinx.html.js.onClickFunction
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledButton
import styled.styledSpan

external interface NavigationProps: RProps {
    var buttonSize: LinearDimension
    var timer: Timer
    var onShowAnswerClick: () -> Unit
    var onTimerClick: () -> Unit
    var onNextClick: () -> Unit
}

class Navigation : RComponent<NavigationProps, RState>() {
    override fun RBuilder.render() {
        styledButton {
            css {
                classes = mutableListOf("btn btn-success mr-2")
                width = props.buttonSize
            }
            if (props.timer.show) +"${props.timer.time / 60 % 60} : ${props.timer.time % 60} " else +" "
            attrs {
                onClickFunction = {
                    props.onTimerClick()
                }
            }
            if (props.timer.show) {
                styledSpan {
                    css {
                        classes = mutableListOf("badge badge-light")
                    }
                    +"${props.timer.count}"
                }
            }
        }
        styledButton {
            css {
                classes = mutableListOf("btn btn-danger mr-2")
                width = props.buttonSize
            }
            +""
            attrs {
                onClickFunction = {
                    props.onShowAnswerClick()
                }
            }
        }
        styledButton {
            css {
                classes = mutableListOf("btn btn-success")
                width = props.buttonSize
            }
            +""
            attrs {
                onClickFunction = {
                    props.onNextClick()
                }
            }
        }
    }
}

fun RBuilder.navigation(handler: NavigationProps.() -> Unit): ReactElement {
    return child(Navigation::class) {
        this.attrs(handler)
    }
}
