import kotlinx.css.paddingLeft
import kotlinx.css.paddingTop
import kotlinx.css.px
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledDiv

external interface StatisticsDisplayProps: RProps {
    var statisticsState: StatisticsState
}

class StatisticsDisplay : RComponent<StatisticsDisplayProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                paddingTop = 10.px
                paddingLeft = 10.px
            }
            statisticsCard {
                title = "  "
                letters = props.statisticsState.topLettersInAll
            }
            statisticsCard {
                title = "    "
                letters = props.statisticsState.topFirstLetters
            }
        }
    }
}

fun RBuilder.statisticsDisplay(handler: StatisticsDisplayProps.() -> Unit): ReactElement {
    return child(StatisticsDisplay::class) {
        this.attrs(handler)
    }
}
