import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement

external interface ImageProps: RProps {
    var selectedThirukkural: Thirukkural
    var showAnswer: Boolean
}

class Image : RComponent<ImageProps, RState>() {
    override fun RBuilder.render() {
        questionImage {
            selectedThirukkural = props.selectedThirukkural
        }
        if (props.showAnswer) {
            kuralDisplay {
                selectedThirukkural = props.selectedThirukkural
            }
        }
    }
}

fun RBuilder.image(handler: ImageProps.() -> Unit): ReactElement {
    return child(Image::class) {
        this.attrs(handler)
    }
}
