import kotlin.random.Random

enum class Topic(val tamil: String) {
    Athikaram(""),
    KuralSol(""),
    Image(""),
    KuralPorul(""),
    AthikaramPorul(" "),
    Tile(" "),
    KuralEluthu(" "),
    Statistics(" "),
    AllKurals("");

    companion object {
        fun getTopic(tamil: String): Topic {
            return values().first { it.tamil == tamil }
        }
    }
}

data class ThirukkuralState(override val index: Int, override var history: MutableList<Int>, val kurals: List<Thirukkural>): HistoryState {
    constructor(thirukkurals: List<Thirukkural>): this(nextIndex(0, thirukkurals.size), mutableListOf(), thirukkurals)
    fun getCurrent(): Thirukkural = kurals[index]
    fun getNext(): ThirukkuralState = this.copy(index = getNext(kurals.size))
}

data class AthikaramState(override val index: Int, override var history: MutableList<Int>, val athikarams: List<String>): HistoryState {
    constructor(thirukkurals: List<Thirukkural>): this(thirukkurals.map { it.athikaram }.distinct())
    constructor(athikarams: List<String>) : this(nextIndex(0, athikarams.size), mutableListOf(), athikarams)
    fun getCurrent(): String = athikarams[index]
    fun getNext(): AthikaramState = this.copy(index = getNext(athikarams.size))
}

interface HistoryState {
    val index: Int
    var history: MutableList<Int>
    fun getNext(maxIndex: Int): Int {
        if (history.isEmpty()) {
            history = generateRandomList(maxIndex)
        }
        val nextIndex = history.removeFirst()
        history.add(nextIndex)
        println("Current: $index to New: $nextIndex of Total: $maxIndex")
        return nextIndex
    }
}

fun generateRandomList(maxIndex: Int): MutableList<Int> {
    var count = maxIndex - 1
    val randomList = generateSequence { (count--).takeIf { it >= 0 } }.toMutableList()
    randomList.shuffle()
    return randomList
}

data class WordState(override val index: Int, override var history: MutableList<Int>, val words: List<String>):HistoryState {
    constructor(thirukkurals: List<Thirukkural>): this(thirukkurals.map { it.words }.flatten().distinct())
    constructor(words: List<String>) : this(nextIndex(0, words.size), mutableListOf(), words)
    fun getCurrent(): String = words[index]
    fun getNext(): WordState = this.copy(index = getNext(words.size))
}

data class LetterState(
    val thirukkural: Thirukkural,
    val kuralWordsSrc: List<List<List<String>>>,
    val kuralWordsTarget: List<List<MutableList<String>>>,
    var elethukkal: List<MutableMap<String, Boolean>>,
    var noOfAttempts: Int
) {
    constructor(thirukkural: Thirukkural) : this(
        thirukkural,
        getLetters(thirukkural),
        initializeLetters(thirukkural),
        initializeButtons(true),
        0
    )
}

private fun getLetters(thirukkural: Thirukkural): List<List<List<String>>> {
    return listOf(getFirstLineLetters(thirukkural), getSecondLineLetters(thirukkural))
}

private fun getFirstLineLetters(thirukkural: Thirukkural): List<List<String>> {
    return thirukkural.words.take(4).map { getCharList(it).toList() }
}

private fun getSecondLineLetters(thirukkural: Thirukkural): List<List<String>> {
    return thirukkural.words.takeLast(3).map { getCharList(it).toList() }
}

fun initializeLetters(thirukkural: Thirukkural): List<List<MutableList<String>>> {
    return listOf(initializeFirstLineLetters(thirukkural), initializeSecondLineLetters(thirukkural))
}

fun fillLetters(thirukkural: Thirukkural): List<List<MutableList<String>>> {
    return listOf(
        getFirstLineLetters(thirukkural).map { it.toMutableList() },
        getSecondLineLetters(thirukkural).map { it.toMutableList() })
}

private fun initializeFirstLineLetters(thirukkural: Thirukkural): List<MutableList<String>> {
    return thirukkural.words.take(4).map { initializeUnderscore(it) }
}

private fun initializeSecondLineLetters(thirukkural: Thirukkural): List<MutableList<String>> {
    return thirukkural.words.takeLast(3).map { initializeUnderscore(it) }
}

private fun initializeUnderscore(it: String) = MutableList(getCharList(it).size) { "_" }

fun disableButtons() = initializeButtons(false)

private fun initializeButtons(state: Boolean): List<MutableMap<String, Boolean>> {
    val uyirLettersMap = uyirLetters.map { it to state }.toMap().toMutableMap()
    val meiLettersMap = meiLetters.map { it to state }.toMap().toMutableMap()
    return listOf(uyirLettersMap, meiLettersMap)
}

data class FilteredKuralState(val index: Int, val kurals: List<Thirukkural>, val wordSizes: List<Int>) {
    constructor(thirukkurals: List<Thirukkural>): this(0, thirukkurals, getWordSize(thirukkurals))
}

fun getWordSize(thirukkurals: List<Thirukkural>): List<Int> {
    return listOf(0) + thirukkurals.map { getCharList(it.words[0]).size }.toList().distinct().sorted()
}

data class Timer(val time: Long = 0, val show:Boolean = false, val count: Int = 0)

fun nextIndex(currentIndex: Int, maxIndex: Int): Int {
    var newIndex: Int
    do {
        newIndex = Random.nextInt(maxIndex)
    } while (newIndex == currentIndex && maxIndex != 1)
    println("Current: $currentIndex to New: $newIndex of Total: $maxIndex")
    return newIndex
}

