data class StatisticsState(val topLettersInAll: Map<String, Int>, val topFirstLetters: Map<String, Int>) {
    constructor(thirukkurals: List<Thirukkural>) : this(getTopLettersInAll(thirukkurals), getTopFirstLetters(thirukkurals))
}

private fun getTopLettersInAll(thirukkurals: List<Thirukkural>): Map<String, Int> {
    val result = mutableMapOf<String, Int>()
    val allCharsList = thirukkurals.map { getLetters(it) }.flatten().flatten().flatten()
    allCharsList.forEach {
        val keyLetter = keyLetters[it] ?: error("Missing $it ${it.toCharArray()[0].toInt()}")
        val initialValue = result.getOrPut(keyLetter, { 0 })
        result[keyLetter] = initialValue + 1
    }
    return result.toList().sortedByDescending { (_, value) -> value }.toMap()
}

private fun getTopFirstLetters(thirukkurals: List<Thirukkural>): Map<String, Int> {
    val result = mutableMapOf<String, Int>()
    val allCharsList = thirukkurals.map { listOf(getCharList(it.words[0])[0], getCharList(it.words[4])[0]) }.flatten()
    allCharsList.forEach {
        val keyLetter = keyLetters[it] ?: error("Missing $it ${it.toCharArray()[0].toInt()}")
        val initialValue = result.getOrPut(keyLetter, { 0 })
        result[keyLetter] = initialValue + 1
    }
    return result.toList().sortedByDescending { (_, value) -> value }.toMap()
}

fun getCharList(line: String): MutableList<String> {
    val list = mutableListOf<String>()
    val charArray = line.toCharArray()
    for (char in charArray) {
        when (char.toInt()) {
            // Space, Dot
            32, 39, 44, 46, 58, 8204 -> {
                // do nothing
            }
            in 3006..3021 -> {
                list[list.size - 1] = list[list.size - 1] + char
            }
            else -> {
                list.add(char.toString())
            }
        }
    }
    return list
}

data class TileTimerState(var time: Long = 300, var count: Int = 0)

data class TileState(
    var tiles: MutableList<TileItem>,
    var activeTileIndex: Int? = null,
    var timeout: Boolean = false
) {
    constructor(thirukkurals: List<Thirukkural>) : this(getTileItems(thirukkurals))
}

fun getTileItems(thirukkurals: List<Thirukkural>): MutableList<TileItem> {
    val topicList = mutableListOf(Topic.Athikaram, Topic.KuralSol, Topic.KuralPorul, Topic.Image)
    val filteredThirukkurals = thirukkurals.shuffled().map { it.athikaramNo to it }.toMap().values.take(20).toMutableList()
    val randomTopics: MutableList<Topic> = mutableListOf()
    val allTiles: MutableList<TileItem> = mutableListOf()
    for (i in 0..4) {
        topicList.shuffle()
        randomTopics.addAll(topicList)
    }
    for (i in 0..19) {
        val thirukkural = filteredThirukkurals.removeFirst()
        val tileItem = TileItem(
            randomTopics.removeFirst(),
            showAnswer = false,
            answered = false,
            thirukkural = thirukkural,
            sol = thirukkural.words[Random.nextInt(thirukkural.words.size)]
        )
        allTiles.add(tileItem)
    }
    return allTiles
}

data class TileItem(
    val type: Topic,
    var showAnswer: Boolean,
    var answered: Boolean,
    val thirukkural: Thirukkural,
    val sol: String
)
