import kotlinx.css.pct
import kotlinx.css.px
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledDiv

external interface PersonProps: RProps {
    var thirukkurals: List<Thirukkural>
    var athikaram: AthikaramState
    var thirukkuralState: ThirukkuralState
    var wordState: WordState
    var letterState: LetterState
    var filteredKuralState: FilteredKuralState
    var statisticsState: StatisticsState
    var tileState: TileState
    var tileTimerState: TileTimerState
    var timer: Timer
    var persons: List<String>
    var showAnswer: Boolean
    var selectedTopic: Topic
    var selectedPerson: String
    var onPersonClick: (String) -> Unit
    var onShowAnswerClick: (Boolean) -> Unit
    var onTileShowAnswerClick: () -> Unit
    var onTimerClick: () -> Unit
    var onTopicClick: (Topic) -> Unit
    var onNextClick: () -> Unit
    var onLetterClick: (String) -> Unit
    var onFilterClick: (Int) -> Unit
    var onTileItemClick: (Int) -> Unit
    var onBackClick: (Boolean) -> Unit
    var onRetryClick: () -> Unit
}

class Person : RComponent<PersonProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            // Desktop
            css {
                classes = mutableListOf("d-none d-lg-block")
            }
            titleBar {
                persons = props.persons
                selectedPerson = props.selectedPerson
                selectedTopic = props.selectedTopic
                filteredKuralState = props.filteredKuralState
                timer = props.timer
                tileTimerState = props.tileTimerState
                firstRowStyle = "col pl-0 pr-0"
                personButtonWidth = 200.px
                topicButtonWidth = 200.px
                secondRowStyle = "col-md-auto pr-0"
                navigationWidth = 120.px
                onPersonClick = { it -> props.onPersonClick(it) }
                onTopicClick = { it -> props.onTopicClick(it) }
                onFilterClick = { it -> props.onFilterClick(it) }
                onShowAnswerClick = { it -> props.onShowAnswerClick(it) }
                onNextClick = { props.onNextClick() }
                onTimerClick = { props.onTimerClick() }
                onRetryClick = props.onRetryClick
            }
        }
        styledDiv {
            // Mobile
            css {
                classes = mutableListOf("d-block d-lg-none")
            }
            titleBar {
                persons = props.persons
                selectedPerson = props.selectedPerson
                selectedTopic = props.selectedTopic
                filteredKuralState = props.filteredKuralState
                timer = props.timer
                tileTimerState = props.tileTimerState
                firstRowStyle = "d-flex flex-row"
                firstRowWidth = 100.pct
                personButtonWidth = 100.pct
                topicButtonWidth = 100.pct
                secondOptionalRowStyle = "d-flex flex-row mt-2"
                secondRowStyle = "d-flex flex-row"
                secondRowWidth = 100.pct
                allKuralsWidth = 100.pct
                navigationWidth = 100.pct
                onPersonClick = { it -> props.onPersonClick(it) }
                onTopicClick = { it -> props.onTopicClick(it) }
                onFilterClick = { it -> props.onFilterClick(it) }
                onShowAnswerClick = { it -> props.onShowAnswerClick(it) }
                onNextClick = { props.onNextClick() }
                onTimerClick = { props.onTimerClick() }
                onRetryClick = props.onRetryClick
            }
        }
        topicContent {
            selectedTopic = props.selectedTopic
            allThirukkurals = props.thirukkurals
            filteredKuralState = props.filteredKuralState
            thirukkuralState = props.thirukkuralState
            athikaram = props.athikaram
            wordState = props.wordState
            letterState = props.letterState
            statisticsState = props.statisticsState
            tileState = props.tileState
            tileTimerState = props.tileTimerState
            timer = props.timer
            showAnswer = props.showAnswer
            onShowAnswerClick = props.onShowAnswerClick
            onTileShowAnswerClick = props.onTileShowAnswerClick
            onTimerClick = props.onTimerClick
            onNextClick = props.onNextClick
            onLetterClick = props.onLetterClick
            onFilterClick = props.onFilterClick
            onTileItemClick = props.onTileItemClick
            onBackClick = props.onBackClick
        }
    }
}

fun RBuilder.person(handler: PersonProps.() -> Unit): ReactElement {
    return child(Person::class) {
        this.attrs(handler)
    }
}
