import kotlinx.css.px
import kotlinx.css.width
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledDiv
import styled.styledH6
import styled.styledSpan

external interface StatisticsCardProps: RProps {
    var title: String
    var letters: Map<String, Int>
}

class StatisticsCard : RComponent<StatisticsCardProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = mutableListOf("card mr-3")
            }
            styledDiv {
                css {
                    classes = mutableListOf("card-body")
                }
                styledH6 {
                    css {
                        classes = mutableListOf("card-title")
                    }
                    +props.title
                }
                styledDiv {
                    for (entry in props.letters) {
                        styledSpan {
                            css {
                                classes = mutableListOf("badge badge-pill badge-primary mr-1")
                                width = 70.px
                            }
                            +"${entry.key} : ${entry.value}"
                        }
                    }
                }

            }
        }

    }
}

fun RBuilder.statisticsCard(handler: StatisticsCardProps.() -> Unit): ReactElement {
    return child(StatisticsCard::class) {
        this.attrs(handler)
    }
}
