import kotlinx.browser.window
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledDiv

external interface TileProps: RProps {
    var tileState: TileState
    var tileTimerState: TileTimerState
    var onTileItemClick: (Int) -> Unit
}

class Tile : RComponent<TileProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = mutableListOf("container-fluid pl-0 pr-0")
            }
            val chunkSize = if (window.innerWidth > 700) 5 else 3
            val chunked = props.tileState.tiles.chunked(chunkSize)
            for ((index, row) in chunked.withIndex()) {
                tileRow {
                    tiles = row
                    tileTimerState = props.tileTimerState
                    onTileItemClick = { column -> props.onTileItemClick((index * chunkSize) + column) }
                }
            }
        }
    }
}

fun RBuilder.tile(handler: TileProps.() -> Unit): ReactElement {
    return child(Tile::class) {
        this.attrs(handler)
    }
}
