import kotlinx.css.fontSize
import kotlinx.css.pct
import kotlinx.css.px
import kotlinx.css.width
import kotlinx.html.js.onClickFunction
import kotlinx.html.role
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledButton
import styled.styledDiv
import styled.styledSpan

external interface KuralEluthuProps: RProps {
    var letterState: LetterState
    var showAnswer: Boolean
    var onLetterClick: (String) -> Unit
}

class KuralEluthu : RComponent<KuralEluthuProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = when (props.letterState.noOfAttempts) {
                    in 0..5 -> mutableListOf("alert alert-success m-2")
                    in 6..9 -> mutableListOf("alert alert-warning m-2")
                    else -> mutableListOf("alert alert-danger m-2")
                }
            }
            var kuralInfo = ""
            if (props.showAnswer) {
                kuralInfo = ",  : [${props.letterState.thirukkural.athikaramNo}.${props.letterState.thirukkural.athikaram}],  : [${props.letterState.thirukkural.kuralNo}]"
            }
            +"No of attempts [${props.letterState.noOfAttempts}]$kuralInfo"
        }
        styledDiv {
            props.letterState.kuralWordsTarget.forEach { line ->
                styledDiv {
                    css {
                        classes = mutableListOf("m-2")
                    }
                    line.forEach { word ->
                        styledDiv {
                            css {
                                classes = mutableListOf("btn-group mr-2")
                            }
                            attrs {
                                role = "group"
                            }
                            word.forEach { letter ->
                                styledButton {
                                    css {
                                        classes = getResultColor(letter == "_")
                                    }
                                    +letter
                                }
                            }
                        }
                    }
                }
            }
        }
        styledDiv {
            css {
            }
            for ((lineIndex, line) in props.letterState.elethukkal.withIndex()) {
                styledDiv {
                    css {
                        classes = mutableListOf("d-flex flex-wrap m-2")
                    }
                    for ((letterIndex, letter) in line.entries.withIndex()) {
                        val index = (lineIndex * 13) + letterIndex + 1
                        styledButton {
                            css {
                                classes = getButtonStyle(letter.value)
                                width = 50.px
                            }
                            +letter.key
                            attrs {
                                onClickFunction = {
                                    if (letter.value) props.onLetterClick(letter.key)
                                }
                            }
                            styledSpan {
                                css {
                                    classes = mutableListOf("badge badge-light")
                                    fontSize = 60.pct
                                }
                                +"$index"
                            }
                        }
                    }
                }
            }
        }
    }

    private fun getResultColor(hide: Boolean): MutableList<String> {
        val style = if (hide) "-outline" else ""
        return mutableListOf("btn btn$style-success btn-sm")
    }

    private fun getButtonStyle(show: Boolean): MutableList<String> {
        val enabled = if (show) "" else "disabled"
        return mutableListOf("btn btn-primary mr-1 $enabled")
    }
}

fun RBuilder.kuralEluthu(handler: KuralEluthuProps.() -> Unit): ReactElement {
    return child(KuralEluthu::class) {
        this.attrs(handler)
    }
}
