import kotlinx.css.LinearDimension
import kotlinx.css.px
import kotlinx.css.width
import kotlinx.html.js.onClickFunction
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledButton
import styled.styledSpan

external interface TileTimerProps: RProps {
    var buttonSize: LinearDimension
    var tileTimerState: TileTimerState
    var onRetryClick: () -> Unit
}

class TileTimer : RComponent<TileTimerProps, RState>() {
    override fun RBuilder.render() {
        styledButton {
            css {
                val btnStyle = if (props.tileTimerState.time > 0) "success" else "danger"
                classes = mutableListOf("btn btn-$btnStyle mr-2")
                width = props.buttonSize
            }
            +"${props.tileTimerState.time / 60 % 60} : ${props.tileTimerState.time % 60} "
            styledSpan {
                css {
                    classes = mutableListOf("badge badge-light")
                }
                +"${props.tileTimerState.count}"
            }
        }
        styledButton {
            css {
                classes = mutableListOf("btn btn-success")
                width = props.buttonSize
            }
            +""
            attrs {
                onClickFunction = {
                    props.onRetryClick()
                }
            }
        }

    }
}

fun RBuilder.tileTimer(handler: TileTimerProps.() -> Unit): ReactElement {
    return child(TileTimer::class) {
        this.attrs(handler)
    }
}
