import kotlinx.css.Align
import kotlinx.css.Color
import kotlinx.css.LinearDimension
import kotlinx.css.alignItems
import kotlinx.css.borderColor
import kotlinx.css.maxWidth
import kotlinx.css.pct
import kotlinx.css.px
import kotlinx.css.width
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledDiv
import styled.styledImg

external interface QuestionImageProps: RProps {
    var selectedThirukkural: Thirukkural
}

class QuestionImage : RComponent<QuestionImageProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = mutableListOf("card m-2")
                alignItems = Align.center
            }
            styledDiv {
                css {
                    maxWidth = 800.px
                }
                styledImg(src = "https://github.com/dreamuth/dreamuth.github.io/raw/master/image/${props.selectedThirukkural.kuralNo}.jpg") {
                    css {
                        classes = mutableListOf("card-img-top")
                    }
                }
            }
        }
    }
}

fun RBuilder.questionImage(handler: QuestionImageProps.() -> Unit): ReactElement {
    return child(QuestionImage::class) {
        this.attrs(handler)
    }
}
